<template>
   <div class="container-fluid">
      <div class="row my-3">
         <div class="details-box border-r">
            <p class="title mb-1 text-white">Search for an address</p>
            <gmap-autocomplete :value="description" @place_changed="setPlace" class="input_styling input-group input-group-lg"></gmap-autocomplete>
            <div v-if="weather" @click="showdiv()" class="my-2 mx-auto btn_div">
               <div  v-if="!show" class="w-100 d-flex flex-row justify-content-center align-items-center">
                  <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-arrows-angle-expand" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                  <path fill-rule="evenodd" d="M1.5 10.036a.5.5 0 0 1 .5.5v3.5h3.5a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5v-4a.5.5 0 0 1 .5-.5z"/>
                  <path fill-rule="evenodd" d="M6.354 9.646a.5.5 0 0 1 0 .708l-4.5 4.5a.5.5 0 0 1-.708-.708l4.5-4.5a.5.5 0 0 1 .708 0zm8.5-8.5a.5.5 0 0 1 0 .708l-4.5 4.5a.5.5 0 0 1-.708-.708l4.5-4.5a.5.5 0 0 1 .708 0z"/>
                  <path fill-rule="evenodd" d="M10.036 1.5a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 .5.5v4a.5.5 0 1 1-1 0V2h-3.5a.5.5 0 0 1-.5-.5z"/>
                  </svg>
                  <span class="pl-2 text-white f_14">Show</span>
               </div>
               <div v-else class="w-100 d-flex flex-row justify-content-center align-items-center">
                  <svg  width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-arrows-angle-contract" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                  <path fill-rule="evenodd" d="M9.5 2.036a.5.5 0 0 1 .5.5v3.5h3.5a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5v-4a.5.5 0 0 1 .5-.5z"/>
                  <path fill-rule="evenodd" d="M14.354 1.646a.5.5 0 0 1 0 .708l-4.5 4.5a.5.5 0 1 1-.708-.708l4.5-4.5a.5.5 0 0 1 .708 0zm-7.5 7.5a.5.5 0 0 1 0 .708l-4.5 4.5a.5.5 0 0 1-.708-.708l4.5-4.5a.5.5 0 0 1 .708 0z"/>
                  <path fill-rule="evenodd" d="M2.036 9.5a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 .5.5v4a.5.5 0 0 1-1 0V10h-3.5a.5.5 0 0 1-.5-.5z"/>
                  </svg>
                  <span class="pl-2 text-white f_14">Close</span>
               </div>
            </div>
            <div class="w-100 d-flex flex-row justify-content-center align-items-center py-2" v-if="weather && show">
               <div class="col-5 col-lg-4">
                  <img :src="icon" class="w-100">
               </div>
               <div class="col-7 col-lg-8">
                  <p class="mb-1 text-white f_20">{{weathertemp}}°C</p>
                  <p v-if="!place" class="mb-1 text-white f_14">Weather is currently {{weatherdesc}}</p>
                  <p v-else class="mb-1 text-white f_14">Weather in {{place}} is {{weatherdesc}}</p>
               </div>
            </div>
         </div>
         <div class="col-12">
            <gmap-map
               id="map"
               :center="center"
               :zoom="15"
               style="width: 100%; height: 100vh"
               >
               <gmap-marker
                  :key="index"
                  v-for="(m, index) in markers"
                  :position="center = m.position"
                  :clickable="true"
                  :draggable="true"
                  @click="center=m.position"
                  ></gmap-marker>
            </gmap-map>
         </div>
      </div>
   </div>
</template>
<script>
   /* eslint-disable */
   import axios from "axios";
   export default {
   data(){
         return {
               center: {lat: 3.139003, lng: 101.686855},
               markers: [
                  {position: {lat: 3.139003, lng: 101.686855}}
               ],
               getMap: this.$root.mapping,
               description: '',
               latLng: {},
               place: "",
               weather: false,
               icon: "",
               weathertitle: "",
               weatherdesc: "",
               weathertemp: "",
               show: true
         }
      },
      mounted(){
         if (navigator.geolocation) {
               navigator.geolocation.getCurrentPosition(function (position) {
                  let pos = {
                     lat: position.coords.latitude,
                     lng: position.coords.longitude
                  };
                  this.center.lat = pos.lat;
                  this.center.lng = pos.lng;
                  this.markers[0].position.lat = pos.lat;
                  this.markers[0].position.lng = pos.lng;
   
                  this.geocodeLatLng(new google.maps.Geocoder, pos, google.maps.InfoWindow);
                  this.getWeather(this.center.lat,this.center.lng);
               }.bind(this));
         }else{
            this.getWeather(this.center.lat,this.center.lng);
         }


      },
      methods: {
         showdiv(){
            this.show = !this.show;
         },
         setDescription(description){
               this.description = description
         },
         setPlace(place){
               console.log(JSON.stringify(place,null,2));
               this.latLng = {
                  lat: place.geometry.location.lat(),
                  lng: place.geometry.location.lng(),
               }
               this.place = place.formatted_address;
               this.center.lat =  place.geometry.location.lat();
               this.center.lng = place.geometry.location.lng();
               this.markers[0].position.lat = place.geometry.location.lat();
               this.markers[0].position.lng = place.geometry.location.lng();
               this.getWeather(place.geometry.location.lat(),place.geometry.location.lng());
         },
         geocodeLatLng(geocoder, map, infowindow){
               geocoder.geocode({'location':this.center}, function(results, status){
                  console.log('localtion' + results, status); 
               });
         },
         usePlace(place) {
            if (this.place) {
            this.markers.push({
               position: {
                  lat: this.place.geometry.location.lat(),
                  lng: this.place.geometry.location.lng(),
               }
            })
            this.place = null;
            }
         },
         getWeather(lat,lng){
            var self = this;
            var latitude = lat;
            var longitude = lng;
            try {
            axios
               .get(
                  "https://api.openweathermap.org/data/2.5/weather?lat="+latitude+"&lon="+longitude+"&appid=dcac1676bb68220c5e0cebddaa19e3ba"
               )
               .then(function(response) {
                  self.setWeatherTemplate(response.data)
               })
               .catch(function(error) {
                  // handle error
                  // console.log(error);
               });
            } catch (e) {
            // console.log(e);
            }
         },
         setWeatherTemplate(obj){
            // console.log(JSON.stringify(obj,null,2))
            this.weather = true;
            this.icon = "http://openweathermap.org/img/wn/"+obj.weather[0].icon+"@4x.png";
            this.weathertitle = obj.weather[0].main;
            this.weatherdesc = obj.weather[0].description;
            this.weathertemp = this.toCelcius(obj.main.temp);
         },
         toCelcius(str) {
            if (str < (0)) {
               return 'below absolute zero (0 K)';
            } else {
               return (str-273.15).toFixed(2);
            }
         }
      }
   }
</script>
